
#overlay[data-v-9118a436] {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        background: #000;
        opacity: 0.8;
        filter: alpha(opacity=80);
        display: block;
  	    z-index: 9999;
}
    /* Timer*/
.timer[data-v-9118a436]{
      z-index: 999;
      width: 29px;
      height: 29px;
      background-color: transparent;
      box-shadow: inset 0px 0px 0px 2px #fff;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -50px;
      margin-left: -100px;
}
.timer[data-v-9118a436]:after, .timer[data-v-9118a436]:before{
      position: absolute;
      content:"";
      background-color: #fff;
}
.timer[data-v-9118a436]:after{
      width: 11px;
      height: 2px;
      top: 13.5px;
      left: 13.5px;
      -webkit-transform-origin: 1px 1px;
          transform-origin: 1px 1px;
      -webkit-animation: minhand 2s linear infinite;
          animation: minhand 2s linear infinite;
}
.timer[data-v-9118a436]:before{
      width: 10px;
      height: 2px;
      top: 13.5px;
      left: 13.5px;
      -webkit-transform-origin: 1px 1px;
          transform-origin: 1px 1px;
      -webkit-animation: hrhand 8s linear infinite;
          animation: hrhand 8s linear infinite;
}
@-webkit-keyframes minhand{
0%{-webkit-transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg)
}
}
@keyframes minhand{
0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}
@-webkit-keyframes hrhand{
0%{-webkit-transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg)
}
}
@keyframes hrhand{
0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}
.box[data-v-9118a436] {
      width: 300px;
      height: 100px;
      background: yellow;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -50px;
      margin-left: -100px;
}



